import {
  Component,
  Inject,
  OnInit,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Observable } from 'rxjs';
import {
  EstadosUsuario,
  IAhorrador,
  IDatosModalConfirmar,
  IPayloadBloquearUsuario,
  IPayloadDarDeBaja,
} from '../../modelo/bloquear-ahorrador.interface';
import { LoadingWrapper } from './loadingWrapper';
import { NgIfContext } from '@angular/common';
import { DashboardService } from '@dashboard/servicios/dashboard.service';

@Component({
  selector: 'app-modal-confirmar-bloqueo',
  templateUrl: './modal-confirmar-bloqueo.component.html',
  styleUrls: ['./modal-confirmar-bloqueo.component.scss'],
})
export class ModalConfirmarBloqueoComponent implements OnInit {
  ahorradorConsulta: {
    data$: Observable<IAhorrador>;
    errorLoading$: Observable<boolean>;
  };
  estadosUsuario = EstadosUsuario;
  mostrarMsj;
  msjStatusCode;
  esError;
  esCargando;

  @ViewChild('template', { static: true })
  template: TemplateRef<NgIfContext> | null = null;

  constructor(
    public dialogRef: MatDialogRef<ModalConfirmarBloqueoComponent>,
    @Inject(MAT_DIALOG_DATA) public data: IDatosModalConfirmar,
    private dashboardService: DashboardService
  ) {}

  ngOnInit() {
    this.ahorradorConsulta = new LoadingWrapper<IAhorrador>(
      this.dashboardService.consultarAhorrador(this.data.idUsuario)
    );
  }

  enviarFomulario(body: IDatosModalConfirmar) {
    const { idUsuario, estado, motivo } = body;
    this.mostrarMsj = false;
    this.esCargando = true;
    body.desactivarUsuario
      ? this.darDeBaja({ idUsuario, origen: 'OPERACIONES' })
      : this.modificarAccesoTemporal({ idUsuario, estado, motivo });
  }

  modificarAccesoTemporal(body: IPayloadBloquearUsuario) {
    return this.dashboardService
      .actualizarEstadoUsuario(body)
      .then(() => {
        this.esError = false;
      })
      .catch((error) => {
        this.msjStatusCode = error.error.data.mensaje;
        this.esError = true;
        this.esCargando = false;
      })
      .finally(() => {
        this.mostrarMsj = true;
        setTimeout(() => {
          this.dialogRef.close(this.mostrarMsj);
        }, 10000);
      });
  }

  darDeBaja(body: IPayloadDarDeBaja) {
    return this.dashboardService
      .darDeBaja(body)
      .then((respuesta: IPayloadDarDeBaja) => {
        this.esError = false;
      })
      .catch((error) => {
        this.msjStatusCode = error.data.mensaje;
        this.esError = true;
        this.esCargando = false;
      })
      .finally(() => {
        this.mostrarMsj = true;
      });
  }
}
