import { AfterViewInit, Component, OnInit } from '@angular/core';
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { MatDialog } from '@angular/material/dialog';
import { ModalConfirmarBloqueoComponent } from '../../componentes/modal-confirmar-bloqueo/modal-confirmar-bloqueo.component';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'baja-usuario',
  templateUrl: './baja-usuario.component.html',
  styleUrls: ['./baja-usuario.component.scss'],
  animations: [
    trigger('changeState', [
      state(
        'entrada',
        style({
          backgroundColor: '#f5f5f5',
          transform: 'scale(1)',
          visibility: 'visible',
        })
      ),
      state(
        'salida',
        style({
          transform: 'scale(0.8)',
        })
      ),
      transition('*=>entrada', animate('500ms')),
      transition('*=>salida', animate('12000ms')),
    ]),
  ],
})
export class BajaUsuarioComponent implements OnInit {
  currentState = 'entrada';
  formIcono = 'lock';
  bloquearForm: FormGroup;

  constructor(public dialog: MatDialog, private fb: FormBuilder) {}

  ngOnInit(): void {
    this.bloquearForm = this.crearFormulario(this.fb);
  }

  openDialog(dato: { idUsuario: string }) {
    const data = { ...dato, desactivarUsuario: true };
    const dialogRef = this.dialog.open(ModalConfirmarBloqueoComponent, {
      data,
    });
    dialogRef.afterClosed().subscribe((result) => {
      this.currentState = 'entrada';
      if (result) {
        this.reiniciarFormulario();
      }
    });
    return dialogRef;
  }

  crearFormulario(fb: FormBuilder): FormGroup {
    return fb.group({
      idUsuario: fb.control('', [
        Validators.required,
        Validators.pattern(
          '^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$'
        ),
      ]),
    });
  }

  reiniciarFormulario() {
    this.bloquearForm.reset();
    this.bloquearForm.controls.idUsuario.updateValueAndValidity();
  }
}
