import { Component, AfterViewInit } from '@angular/core';
import {
  trigger,
  state,
  style,
  transition,
  animate,
} from '@angular/animations';
import { MatDialog } from '@angular/material/dialog';
import { ModalConfirmarBloqueoComponent } from '../../componentes/modal-confirmar-bloqueo/modal-confirmar-bloqueo.component';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import {
  EstadosUsuario,
  IPayloadBloquearUsuario,
} from '../../modelo/bloquear-ahorrador.interface';
import { threadId } from 'worker_threads';

@Component({
  selector: 'app-bloquear-acceso',
  templateUrl: './bloquear-acceso.component.html',
  styleUrls: ['./bloquear-acceso.component.scss'],
  animations: [
    trigger('changeState', [
      state(
        'entrada',
        style({
          backgroundColor: '#f5f5f5',
          transform: 'scale(1)',
          visibility: 'visible',
        })
      ),
      state(
        'salida',
        style({
          transform: 'scale(0.8)',
        })
      ),
      transition('*=>entrada', animate('500ms')),
      transition('*=>salida', animate('12000ms')),
    ]),
  ],
})
export class BloquearAccesoComponent implements AfterViewInit {
  currentState = 'entrada';
  formIcono = 'lock';

  esAccionSeleccionada = false;
  esDesbloqueo = false;
  esOtroMotivo = false;

  EstadosUsuarioMapping: Record<EstadosUsuario, string> = {
    [EstadosUsuario.ACTIVADO]: 'Activar',
    [EstadosUsuario.BLOQUEADO]: 'Desactivar',
  };

  public listaEstadoUsuario = Object.values(EstadosUsuario);

  bloquearForm: FormGroup;
  constructor(public dialog: MatDialog, private fb: FormBuilder) {
    this.bloquearForm = this.crearFormulario(fb);
  }

  ngAfterViewInit(): void {}

  openDialog(data: IPayloadBloquearUsuario): void {
    const dialogRef = this.dialog.open(ModalConfirmarBloqueoComponent, {
      data,
      panelClass: 'medium-screen-dialog',
    });
    dialogRef.afterClosed().subscribe((result) => {
      this.currentState = 'entrada';
      result && this.reiniciarFormulario();
    });
  }

  motivosDesbloqueo: string[] = [
    'Solicitud de PLD',
    'Devolución de contracargo',
    'Corrección de identificación',
    'Otro',
  ];

  motivosBloqueo: string[] = [
    'Solicitud de PLD',
    'Contracargo',
    'Identificación no corresponde a usuario',
    'Identificación alterada',
    'Otro',
  ];

  accionSeleccionada(valor) {
    this.formIcono = valor.value == 'ACTIVADO' ? 'lock_open' : 'lock';
    this.esAccionSeleccionada = true;
    this.esDesbloqueo = valor.value == 'ACTIVADO' ? true : false;
  }

  motivoSeleccionado(valor) {
    if (valor.value == 'Otro') {
      this.esOtroMotivo = true;
      this.bloquearForm.controls.motivo = this.bloquearForm.controls.otroMotivo;
      this.bloquearForm.controls.otroMotivo.setValidators([
        Validators.required,
      ]);
      this.bloquearForm.controls.otroMotivo.updateValueAndValidity();
    } else {
      this.bloquearForm.controls.otroMotivo.clearValidators();
      this.bloquearForm.controls.otroMotivo.updateValueAndValidity();
      this.esOtroMotivo = false;
    }
  }

  crearFormulario(fb: FormBuilder): FormGroup {
    return fb.group({
      idUsuario: fb.control('', [
        Validators.required,
        Validators.pattern(
          '^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$'
        ),
      ]),
      estado: fb.control('', [Validators.required]),
      motivo: fb.control('', [Validators.required]),
      otroMotivo: fb.control(''),
    });
  }

  reiniciarFormulario() {
    this.bloquearForm.reset();
    this.bloquearForm.controls.idUsuario.clearValidators();
    this.bloquearForm.controls.idUsuario.updateValueAndValidity();
    this.bloquearForm.controls.estado.clearValidators();
    this.bloquearForm.controls.estado.updateValueAndValidity();
    this.bloquearForm.controls.motivo.clearValidators();
    this.bloquearForm.controls.motivo.updateValueAndValidity();
    this.esOtroMotivo = false;
    this.esAccionSeleccionada = false;
  }
}
